.layout {
  margin: 0;
  font-size: 4rem;
  line-height: 1.2;

  .text {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .block {
    position: fixed;
    width: 50vw;
    height: 40px;
    bottom: 0px;
    left: 50%;
    transform: translateX(-50%);
    background-color: @cyan;

  }
}
@primary-color: #00adb5;@black: #222831;@white: #fff;@cyan: #DEEAF3;@gray: #888;@dark-gray: #393e46;@blue: #001529;